.innovation {
  padding-top: 50px;
  padding-bottom: 45px;
}

.innovation h2 {
  display: grid;
  grid-template-columns: 1fr;
  padding-left: 120px;
  padding-bottom: 36px;

  @media (max-width: 1600px) {
    padding-left: 0;
  }

  @media (max-width: 960px) {
    text-align: center;
  }
}

.innovation__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 600px));
  column-gap: 5px;
  row-gap: 20px;

  @media (max-width: 1600px) {

  }

  @media (max-width: 1280px) {
    justify-content: center;
  }
}

.innovation__list li {
  position: relative;
  max-width: 600px;
  min-height: 639px;

  outline: 1px solid red;

  @media (max-width: 767px) {
    min-height: 600px;
  }

  @media (max-width: 580px) {
    min-height: 480px;
  }

  @media (max-width: 480px) {
    min-height: 449px;
  }
}

.innovation__list li:hover::before,
.innovation__list li:focus::before {
  position: absolute;
  content: "";
  width: 85%;
  height: 620px;
  top: 19px;
  left: 50%;
  transform: translateX(-50%);
  background-image: url("../img/back-hover-3.svg");
  background-repeat: no-repeat;
  z-index: -1;

  @media (max-width: 767px) {
    height: 581px;
    background-image: url("../img/back-hover-4-767.svg");
  }

  @media (max-width: 580px) {
    background-image: url("../img/back-hover-4-580.svg");
  }

  @media (max-width: 480px) {
    height: 430px;
  }
}

.innovation__list li img:nth-child(2) {
  display: none;
}

.innovation__list li:hover img:nth-child(1) {
  display: none;
}

.innovation__list li:hover img:nth-child(2) {
  display: block;
}

.innovation__btn {
  display: none;
  width: 278px;
  text-decoration: none;
  margin-top: 47px;

  @media (max-width: 767px) {
    width: 60%;
    margin: 0 auto;
    margin-top: 47px;
  }

  @media (max-width: 580px) {
    margin-top: 35px;
  }

  @media (max-width: 480px) {
    width: 75%;
  }
}

.innovation__list li:hover .innovation__btn,
.innovation__list li:focus .innovation__btn {
  display: block;
}

.innovation__list li .innovation__desc {
  position: absolute;
  width: 450px;
  left: 50%;
  transform: translateX(-50%);

  @media (max-width: 767px) {
    width: 75%;
  }
}

.innovation__desc {
  margin-top: 18px;

  @media (max-width: 767px) {
    text-align: center;
  }
}

.innovation__desc h3 {
  font-size: 45px;
  line-height: 34px;
  font-weight: 300;
  letter-spacing: -0.5px;
  margin: 0;
  margin-bottom: 25px;

  @media (max-width: 580px) {
    font-size: 36px;
  }

  @media (max-width: 580px) {
    font-size: 32px;
    line-height: 24px;
  }
}

.innovation__desc p {
  margin: 0;

  @media (max-width: 480px) {
    font-size: 16px;
  }
}

.innovation__list li:nth-child(1) p {
  padding-right: 80px;

  @media (max-width: 767px) {
    padding-right: 0;
  }
}

.innovation__list li:nth-child(3) p {
  padding-right: 120px;

  @media (max-width: 767px) {
    padding-right: 0;
  }
}
