.clean-rooms {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  padding-bottom: 45px;
}

.clean-rooms__slider {
  min-height: 430px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 340px));
  column-gap: 74px;
  row-gap: 40px;
  margin-top: 50px;
  overflow: hidden;

  @media (max-width: 1280px) {
    justify-content: center;
  }
}

.clean-rooms__slider a {
  text-decoration: none;
  cursor: pointer;

  @media (max-width: 1024px) {
    text-align: center;
  }
}

.clean-rooms__slider img {
  margin-bottom: 30px;
}

.clean-rooms h4 {
  font-size: 38px;
  line-height: 26px;
  font-weight: 300;
  letter-spacing: -1px;
  margin: 0;
  margin-bottom: 25px;

  @media (max-width: 580px) {
    font-size: 28px;
    margin-bottom: 18px;
  }
}

.clean-rooms p {
  margin: 0;
}

.clean-rooms h2 {
  @media (max-width: 960px) {
    text-align: center;
  }
}
